.maskContent {
  position: fixed;
  z-index: 100000;
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
}
.maskContent .maskBox {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  width: 5rem;
  height: 5rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #4c4c4c;
  border-radius: 0.2rem;
}
.maskContent .maskBox .img {
  width: 2.125rem;
  height: 2.125rem;
}
.maskContent .maskBox .text {
  font-size: 0.4rem;
  color: white;
  margin-top: 0.25rem;
}
